import React, { useEffect, useState } from 'react';
import { View, Text, StyleSheet, ScrollView, TouchableOpacity, Alert } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { MaterialIcons } from '@expo/vector-icons';

const FavoritesScreen: React.FC = () => {
    const [favorites, setFavorites] = useState<any[]>([]);

    // Load favorites from AsyncStorage
    const loadFavorites = async () => {
        try {
            const storedFavorites = await AsyncStorage.getItem('favorites');
            if (storedFavorites) {
                setFavorites(JSON.parse(storedFavorites));
            }
        } catch (error) {
            console.error('Error loading favorites:', error);
        }
    };

    // Delete a Hadith from favorites
    const deleteFavorite = async (index: number) => {
        const updatedFavorites = [...favorites];
        updatedFavorites.splice(index, 1); // Remove the Hadith at the specified index
        setFavorites(updatedFavorites);

        try {
            await AsyncStorage.setItem('favorites', JSON.stringify(updatedFavorites));
            Alert.alert('Deleted', 'Hadith removed from favorites.');
        } catch (error) {
            console.error('Error deleting favorite:', error);
        }
    };

    useEffect(() => {
        loadFavorites();
    }, []);

    return (
        <ScrollView contentContainerStyle={styles.container}>
            <Text style={styles.title}>Your Favorite Hadiths</Text>
            {favorites.length > 0 ? (
                favorites.map((hadith, index) => (
                    <View key={index} style={styles.hadithContainer}>
                        <View style={styles.hadithTextContainer}>
                            <Text style={styles.hadithTextArabic}>{hadith.text_ar}</Text>
                            <Text style={styles.hadithTextEnglish}>{hadith.text_en}</Text>
                            <Text style={styles.hadithSource}>Source: {hadith.source}</Text>
                        </View>

                        <TouchableOpacity
                            style={styles.deleteButton}
                            onPress={() => deleteFavorite(index)}
                        >
                            <Text style={styles.deleteText}>X</Text>
                        </TouchableOpacity>

                    </View>
                ))
            ) : (
                <Text style={styles.noFavoritesText}>You have no favorites yet.</Text>
            )}
        </ScrollView>
    );
};

const styles = StyleSheet.create({
    container: {
        flexGrow: 1,
        justifyContent: 'center',
        alignItems: 'center',
        padding: 16,
    },
    title: {
        fontSize: 24,
        fontFamily: 'Amiri',
        marginBottom: 16,
    },
    hadithContainer: {
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'space-between',
        marginBottom: 16,
        padding: 16,
        borderWidth: 1,
        borderColor: '#ddd',
        borderRadius: 8,
        width: '100%',
    },
    hadithTextContainer: {
        flex: 1, // Allows the text to take up remaining space
        marginRight: 8,
    },
    hadithTextArabic: {
        fontSize: 20,
        fontFamily: 'Amiri',
        textAlign: 'right',
        marginBottom: 8,
    },
    hadithTextEnglish: {
        fontSize: 18,
        fontFamily: 'Arial',
    },
    hadithSource: {
        fontSize: 14,
        fontStyle: 'italic',
        color: '#555',
    },
    deleteIcon: {
        marginLeft: 8,
    },
    noFavoritesText: {
        fontSize: 18,
        color: '#999',
    },
    deleteButton: {
        backgroundColor: '#F44336', // Red background for delete
        paddingVertical: 8,
        paddingHorizontal: 16,
        borderRadius: 20,
        alignSelf: 'flex-end',
        margin: 5,
        elevation: 4,
    },
    deleteText: {
        color: 'white',
        fontSize: 14,
        fontWeight: 'bold',
    },

});

export default FavoritesScreen;
