import React, { useEffect, useState } from 'react';
import { StatusBar, StyleSheet, View, Text, Button, TouchableOpacity } from 'react-native';
import * as SplashScreen from 'expo-splash-screen';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import HomeScreen from './src/screens/HomeScreen'; // Make sure the path is correct
import SupportScreen from './src/screens/SupportScreen'; // Make sure the path is correct
import FavoritesScreen from './src/screens/FavoritesScreen'; // Make sure the path is correct

// Prevent the splash screen from hiding immediately
SplashScreen.preventAutoHideAsync();

const Stack = createStackNavigator();

export default function App() {
  const [isReady, setIsReady] = useState(false);

  useEffect(() => {
    const prepare = async () => {
      // Load fonts, assets, etc.
      await new Promise(resolve => setTimeout(resolve, 2000)); // Simulate loading
      setIsReady(true);
      SplashScreen.hideAsync();
    };
    prepare();
  }, []);

  if (!isReady) {
    return null; // Show splash screen until app is ready
  }

  return (
    <NavigationContainer>
      <Stack.Navigator initialRouteName="Home">
        <Stack.Screen name="Home" component={HomeScreen} options={{ title: 'Hidaya' }} />
        <Stack.Screen name="Favorites" component={FavoritesScreen} options={{ title: 'Favorites' }} />
        <Stack.Screen name="Support" component={SupportScreen} options={{ title: 'Support' }} />
      </Stack.Navigator>
    </NavigationContainer>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center',
  },
});
