// src/screens/HomeScreen.tsx

import React, { useState, useEffect } from 'react';
import { View, Text, Button, StyleSheet, ScrollView, Alert, TouchableOpacity } from 'react-native';
import { StackNavigationProp } from '@react-navigation/stack';
import { RootStackParamList } from '../../types/navigation';
import AsyncStorage from '@react-native-async-storage/async-storage';
import hadithsData from '../data/hadiths.json'; // Import Hadith data

type HomeScreenNavigationProp = StackNavigationProp<RootStackParamList, 'Home'>;

interface HomeScreenProps {
  navigation: HomeScreenNavigationProp;
}

const HomeScreen: React.FC<HomeScreenProps> = ({ navigation }) => {
  const [randomHadith, setRandomHadith] = useState<any>(null);
  const [favorites, setFavorites] = useState<any[]>([]);

  const getRandomHadith = () => {
    const randomIndex = Math.floor(Math.random() * hadithsData.length);
    setRandomHadith(hadithsData[randomIndex]);
  };

  const loadFavorites = async () => {
    try {
      const storedFavorites = await AsyncStorage.getItem('favorites');
      if (storedFavorites) {
        setFavorites(JSON.parse(storedFavorites));
      }
    } catch (error) {
      console.error('Error loading favorites:', error);
    }
  };

  const addToFavorites = async (hadith: any) => {
    const isDuplicate = favorites.some(
      (fav) => fav.text_ar === hadith.text_ar && fav.text_en === hadith.text_en
    );
    if (isDuplicate) {
      Alert.alert('Duplicate', 'This Hadith is already in your favorites.');
      return;
    }

    const updatedFavorites = [...favorites, hadith];
    setFavorites(updatedFavorites);
    try {
      await AsyncStorage.setItem('favorites', JSON.stringify(updatedFavorites));
      Alert.alert('Success', 'Hadith added to favorites!');
    } catch (error) {
      console.error('Error saving to favorites:', error);
    }
  };

  useEffect(() => {
    getRandomHadith();
    loadFavorites();
  }, []);

  return (
    <ScrollView contentContainerStyle={styles.contentContainer}>
      <Text style={styles.title}>Welcome to Hidaya!</Text>
      <Text style={styles.subtitle}>Hadith App</Text>

      {randomHadith ? (
        <View style={styles.hadithContainer}>
          <Text style={styles.hadithTextArabic}>{randomHadith.text_ar}</Text>
          <Text style={styles.hadithTextEnglish}>{randomHadith.text_en}</Text>
          <Text style={styles.hadithSource}>Source: {randomHadith.source}</Text>

          <TouchableOpacity style={styles.button} onPress={() => addToFavorites(randomHadith)}>
            <Text style={styles.buttonText}>Add to Favorites</Text>
          </TouchableOpacity>
        </View>
      ) : (
        <Text style={styles.loadingText}>Loading random Hadith...</Text>
      )}

      <TouchableOpacity style={styles.button} onPress={getRandomHadith}>
        <Text style={styles.buttonText}>Get New Hadith</Text>
      </TouchableOpacity>

      <TouchableOpacity style={styles.button} onPress={() => navigation.navigate('Favorites')}>
        <Text style={styles.buttonText}>View Favorites</Text>
      </TouchableOpacity>

      <TouchableOpacity style={styles.button} onPress={() => navigation.navigate('Support')}>
        <Text style={styles.buttonText}>Support</Text>
      </TouchableOpacity>
    </ScrollView>
  );
};

const styles = StyleSheet.create({
  contentContainer: {
    flexGrow: 1,
    justifyContent: 'center',
    alignItems: 'center',
    padding: 16,
  },
  title: {
    fontSize: 24,
    fontFamily: 'Amiri', // Arabic font
    color: '#333',
  },
  subtitle: {
    fontSize: 18,
    fontFamily: 'Arial', // English font
    color: '#777',
  },
  hadithContainer: {
    marginVertical: 20,
    padding: 16,
    borderWidth: 1,
    borderColor: '#ddd',
    borderRadius: 8,
    width: '100%',
  },
  hadithTextArabic: {
    fontSize: 20,
    fontFamily: 'Amiri', // Arabic font
    textAlign: 'right',
    marginBottom: 10,
  },
  hadithTextEnglish: {
    fontSize: 18,
    fontFamily: 'Arial', // English text font
    textAlign: 'left',
  },
  hadithSource: {
    fontSize: 14,
    fontStyle: 'italic',
    color: '#555',
    marginTop: 10,
  },
  loadingText: {
    fontSize: 18,
    color: '#999',
  },
  button: {
    backgroundColor: '#4CAF50', // Green background
    paddingVertical: 12,
    paddingHorizontal: 20,
    borderRadius: 8,
    marginTop: 10,
    alignItems: 'center',
    justifyContent: 'center',
    boxShadow: '0px 2px 3px rgba(0, 0, 0, 0.2)', // Box shadow for buttons
    elevation: 4, // Shadow on Android
  },
  buttonText: {
    color: 'white',
    fontSize: 16,
    fontWeight: 'bold',
  },
  deleteButton: {
    backgroundColor: '#F44336', // Red background for delete
    paddingVertical: 8,
    paddingHorizontal: 16,
    borderRadius: 20,
    alignSelf: 'flex-end',
    margin: 5,
    elevation: 4,
  },
  deleteText: {
    color: 'white',
    fontSize: 14,
    fontWeight: 'bold',
  },
});

export default HomeScreen;
